.header_section{
    padding-top: 20px;
    padding-left: 64px;
    padding-right: 64px;
    padding-bottom: 36px;
    background-color: #060A1C;
    position: relative;
    z-index: -5;
}
.header_thumb{
    display:flex;
    align-items: center;
    justify-content: space-between;
}
.locale_thumb{
    display: flex;
    margin-top: 20px;
}
.locale{
    margin-right: 40px;
    color:#fff;
    font-size: 20px;
        font-style: normal;
        font-weight: 500;
        line-height: normal;
}
.header_register_btn{
    padding: 16px 48px;
    justify-content: center;
    align-items: center;
border-radius: 10px;
background: linear-gradient(96deg, #2DA1E1 -12.07%, #024BB1 47.78%, #4720D3 104.01%);
color:#fff;
font-size: 20px;
    font-style: normal;
    font-weight: 600;
    line-height: 24px;
}